@import "mixins";
.Reminder__frame {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    @include for-tablet-landscape-up {
        flex-direction: row;
        justify-content: center;
    }
}
.Reminder {
    font-size: 30px;
    background-color: #F0F3F4;
    &__container  {
        font-family: 'Gloria Hallelujah', cursive;
        line-height: 80px;
        text-shadow: 2px 8px 6px rgba(0,0,0,0.2),0px -5px 35px rgba(255,255,255,0.3);
        overflow: hidden;
        position: relative;
        height: 80px;
        width: 300px;
        margin: 10px 0;
        background-color: #F7F9F9;
        box-shadow: 6px 8px 5px #888;
        &:nth-child(2) {
            @include for-tablet-landscape-up {
                margin: 0 1px;
            }
        }
        &-column {
        width: 100%;
        text-align: center;
        display: block;
        position: relative;
        white-space: nowrap;
        &-moveForward {
            top: -240px;
            animation:move 12s;
            animation-delay: 1.5s;
            animation-iteration-count: 1;
            animation-fill-mode: forwards;
        }

        &-moveBackward {
            top: -320px;
            animation:moveback 12s;
            animation-delay: 1.5s;
            animation-iteration-count: 1;
            animation-fill-mode: forwards;
        }

      }

    }
}
@keyframes move {
    0%  { top: -240px; }
    14% { top: -320px; }
    28% { top: -400px; }
    42% { top: -480px; }
    56% { top: -560px; }
    70% { top: -160px; }
    84% { top: -80px; }
    100% { top: 0px; }
}
@keyframes moveback {
    0%  { top: -320px; }
    14% { top: -240px; }
    28% { top: -160px; }
    42% { top: -80px; }
    56% { top: 0px; }
    70% { top: -400px; }
    84% { top: -480px; }
    100% { top: -560px; }
}
