@import 'mixins';
.grid {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    
    &__el {
        display: content-box;
        height: 350px;
        padding: 20px;
        background-clip: content-box;
        background-size: cover;
        background-position: center center;
        overflow: hidden;
        &-small {
            flex-basis: 100%;
            @include for-tablet-portrait-up {
                flex-basis: 40%;
            }
            @include for-tablet-landscape-up {
                flex-basis: 30%;
            }
        }
        
        &-large {
            flex-basis: 100%;
            @include for-tablet-portrait-up {
                flex-basis: 40%;
            }
            @include for-tablet-landscape-up {
                flex-basis: 60%;
            }
        }
        
        &-video {
            object-fit: cover;
            width: 100%;
            height: 100%;
            border-radius: 1px;
        }

        &-center-right {
            background-position: right top;
        }
    }
}


