@import 'mixins';

.Cover {
    position: relative;
    height: 100vh;
    width: 100vw;
    &__Hero-Image {
        width: 100%;
        height: 100%;
        background-size: cover;
        background-position: center center;
        background-repeat: no-repeat;
        background-image: url(../images/Cover-Phone.jpeg);
        opacity: 0;
        animation: 1s fadein 0.5s forwards;
        @include for-tablet-portrait-up {
            background-image: url(../images/Cover-Tablet-Portrait.jpeg);
        }
        @include for-tablet-landscape-up {
            background-image: url(../images/Cover-Tablet-Landscape.jpeg);
            background-attachment: fixed;
        }
        @include for-desktop-up {
            background-image: url(../images/Cover-Desktop.jpeg);
            background-attachment: fixed;
        }
    }
    &__text-content {
        font-family: 'Gloria Hallelujah', cursive;
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        left: 0;
        width: 100%;
        text-align: center;
        opacity: 0;
        margin-top: 100px;
        animation: 1s slidefade 1s forwards;
    }
    &__title {
        font-weight: 400;
        margin: 0;
        text-shadow: 2px 8px 6px rgba(0,0,0,0.2),0px -5px 35px rgba(255,255,255,0.3);
        font-size: 1.7rem;

        @include for-tablet-portrait-up {
            font-size: 3.2rem;
        }
        @include for-tablet-landscape-up {
            font-size: 3.2rem;
        }
        @include for-desktop-up {
            font-size: 4.6rem;
        }
    }
}
