@import 'mixins';


.Stats__SVG__Container {
    display: block;
    position: relative;
    margin: 0 auto;
    width: 300px;
    height: 300px;
    border: 3px solid black;
    box-shadow: 6px 8px 5px #888;
    @include for-tablet-portrait-up {
        width: 500px;
        height: 500px;

    }
}


.Stats__SVG {
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    // border: 1px solid black;
    &__circle {
        transition: fill 0.3s ease-out;
        box-shadow: 6px 8px 5px #888;
        cursor: pointer;

    }
    &__circle:hover {
        fill: black;
    }
}

.tooltip {
    position: absolute;
    opacity: 0;
    pointer-events: none;
    background-color: transparent;
    border: 1px solid black;
    border-radius: 5px;
    padding: 2px;
    text-align: center;
    transition: opacity 0.3s ease-out;
    width:80%;
    font-size: 10px;
    box-shadow: 6px 8px 5px #888;
    @include for-tablet-portrait-up {
      font-size: 16px;

    }
}
