@import "mixins";

.header {
    padding: 10px 0;
    position: absolute;
    z-index: 2;
    width: 100vw;
    opacity: 0;
    animation: 1s fadein 1.6s forwards;
    &--is-expanded {
        background-color: rgba( #DCD9D6, .55);
    }
    @include for-tablet-portrait-up {
        background-color: rgba( #DCD9D6, 0);
    }
    &--is-back-to-top {
        position: fixed;
        opacity: 1;
        margin-top: 0;
        @include for-tablet-portrait-up {
            background-color: rgba( #DCD9D6, .55);
        }
        animation: 1s headerSlideUpAndBackToTop ease-out forwards;;
    }
    &--slide-down {
        position: fixed;
        opacity: 0;
        margin-top: -60px;
        @include for-tablet-portrait-up {
            background-color: rgba( #DCD9D6, .55);
        }
        animation: 0.5s headerSlideDown ease-out forwards;
    }

    &--slide-up {
        position: fixed;
        opacity: 1;
        margin-top: 0;
        @include for-tablet-portrait-up {
            background-color: rgba( #DCD9D6, .55);
        }
        animation: 0.5s headerSlideUp ease-out forwards;
    }
    &__container {
        display: flex;
        justify-content: space-between;
    }

    &__menu-icon {
        width: 20px;
        height: 19px;
        position: absolute;
        top: 10px;
        right: 10px;
        z-index: 10;
        @include for-tablet-portrait-up {
            display: none;
        }
        &::before {
            content: "";
            position: absolute;
            top: 0;
            left: 0;
            width: 20px; 
            height: 3px;
            background-color: black;
            transform-origin: 0 0;
            transition: transform .3s ease-out;
        }
        &__middle {
            position: absolute;
            top: 8px;
            left: 0 ;
            width: 20px;
            height: 3px;
            background-color: black;
            transition: all .3s ease-out;
            transform-origin: 0 50%;
        }
        &::after {
            content: "";
            position: absolute;
            bottom: 0;
            left: 0 ;
            width: 20px;
            height: 3px;
            background-color: black;
            transform-origin: 0 100%;
            transition: transform .3s ease-out;
        }
        &--close-x {
            &::before {
                transform: rotate(45deg) scaleX(1.25);
            }

            .header__menu-icon__middle {
                opacity: 0;
                transform: scaleX(0);
            }

            &::after {
                transform: rotate(-45deg) scaleX(1.25) translateY(1px);
            }
        }
    }
}

.Patronum {
    position: absolute;
    opacity: 0.06;
    border-radius: 50%;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    transition: transform 0.2s ease-out;
    width: 100px;
    height: 100px;
    background-size: cover;
    background-position: center center;
    background-image: url(../images/MyPatronum.jpeg);
    .header--slide-down & {
        opacity: 0.16;
        transform: translateX(-50%) scale(0.36) translateY(-80%) ;
    }
    .header--slide-up & {
        opacity: 1;
        transform: translateX(-50%) scale(0.86) translateY(-6%) ;
    }
    @include for-tablet-portrait-up {
        left: 10%;
        transform: translateX(0%);
        .header--slide-down & {
            transform: scale(.57) translateY(-40%);
        }
        .header--slide-up & {
            opacity: 1;
            transform: translateY(-10%) ;
        }
    }
}

.lazyload-placeholder {
    @include for-phone-only {
        height: 2200px !important;
    }
}



@keyframes headerSlideDown {
    100% {
        opacity: 1;
        margin-top: 0;
    }
}

@keyframes headerSlideUp {
    100% {
        opacity: 0;
        margin-top: -60px;
    }
}

@keyframes headerSlideUpAndBackToTop {
    80% {
        opacity: 0;
        margin-top: -60px;
    }
    100% {
        position: absolute;
        background-color: rgba( #DCD9D6, 0);
        opacity: 1;
    }
}