.btn {
	cursor: pointer;
    border-radius: 0px;
    text-decoration: none;
    display: inline-block;
    padding: 12px 18px;
    font-size: 1.2rem;
    font-weight: 400;
    line-height: 19px;
    text-transform: uppercase;
    font-family: 'Gloria Hallelujah', cursive;
    letter-spacing: 3px;
    transition: all .4s ease-in-out; 
    outline: none;

    &--long {
        font-size: 12px;
        padding: 6px 6px;
        font-style: italic;
        font-weight: 600;
    }

    &--small {
        font-style: italic;
        padding: 3px 3px;
        width: 100px;
        height: 40px;
        font-size: 1rem;
        font-weight: 300;
        font-weight: 600;
    }

    &--medium {
        font-style: italic;
        padding: 8px 5px;
        font-size: 13px;
        line-height: 15px;
        font-weight: 600;
    } 

    &--large {
        font-style: italic;
        font-weight: 600;
        padding: 18px 24px;
        font-size: 14px;
        line-height: 40px;
    }
    
    &:hover {
        transition: all .4s ease-in-out;
    }
    
    &--white {
        border:solid 2px #fff;
        background: transparent;
        color: #fff;
    }

    &--white:hover, .btn-white.active{
        border:solid 2px #fff;
        background: #fff;
        color: #1f1f1f;
    }
    
    &--dark {
        border:solid 2px #1f1f1f;
        background: transparent;
        color: #1f1f1f;
    }
    &--dark:hover{
        border:solid 2px #1f1f1f;
        background: #1f1f1f;
        color: #fff;
    }

    // This can be used to omit hover effect in mobile devices 
    // @media (hover: hover) { 
    //     &--dark:hover { 
    //         border:solid 2px #1f1f1f;
    //         background: #1f1f1f;
    //         color: #fff;
    //     }
    // }

    &--dark-shutter-effect {
        position: relative;
        background: transparent;
        z-index: 0;
        &::before {
            content: "";
            position: absolute;
            z-index: -1;
            top: 0;
            bottom: 0;
            left: 0;
            right: 0;
            background: black;
            transform: scaleY(0);
            transform-origin: 50%;
            transition-property: transform;
            transition-duration: 0.3s;
            transition-timing-function: ease-out;
        }
        &:hover,
        &:focus,
        &:active {
            color: white;
            &:before {
                transform: scaleY(1);
            }
        }
    }

}



