@import "mixins";

.footer {
    text-align: center;
    padding: 0.5rem 0;
    background-color:#DCD9D6;
    &__text {
        display: block;
        margin-bottom: 1rem;
        font-weight: 200;
        @include for-tablet-landscape-up {
            display: inline;
            margin-bottom: 0;
            margin-right: 1.4rem;
        }
    }
    &__paragraph{
        margin: 0.6em;
    }
}