@import 'mixins';
.letter__form {
    display: none;
    font-size: 16px;
    padding: 30px 30px 15px 30px;
    border: 5px solid black;
    margin: 0 auto;
    position: relative;
    @include for-tablet-portrait-up {
       width: 450px;

   }
   &-is-visible {
       display: block;
       opacity: 0;
       animation: 1s fadein forwards;
   }
   &__close {
       position: absolute;
       top: 6px;
       right: 10px;
       font-size: 1.5rem;
       font-weight: 600;
       transform: scaleX(1.2);
       transform-origin: 100% 0;
       cursor: pointer;
       &:hover {
          font-weight: 500;
       }
   }

    & label {
       display:block;
       margin-bottom: 10px;

       & > span {
           display: block;
           text-align: left;
           top: 0;
           font-style: italic;
           @include for-tablet-portrait-up {
               width: 100px;
               display: inline-block;
               vertical-align: top;
           }
       }

       & input[type=text], & input[type=email] {
           background: transparent;
           border: none;
           border-bottom: 1px dashed black;
           outline: none;
           padding: 0;
           font-weight: 600;
           width: 100%;
           @include for-tablet-portrait-up {
               width: 275px;
           }
       }
       & input[type=text]:focus, & input[type=email]:focus {
           border-bottom: 1px dashed #8F747E;
       }

       & textarea {
           padding: 0;
           background: transparent;
           outline: none;
           border: 1px solid black;
           height: 100px;
           resize: none;
           font-weight: 600;
           width: 100%;
           @include for-tablet-portrait-up {
               width: 275px;
           }

       }

       & textarea:focus {
           border: 1px solid #8F747E;
       }
    }

    &__Input-on-focus__messages, &__Input-on-submit__messages{
        display: none;
        @include for-tablet-landscape-up {
           display: block;
           font-size: 13px;
           font-weight: 600;
           text-align: center;
           margin: 10px auto;
       }
    }

    &--mobile__view__messages {
       display: block;
       font-size: 13px;
       font-weight: 600;
       text-align: center;
       margin: 10px auto;
       @include for-tablet-landscape-up {
           display: none;
       }
    }

    &__button {
        position: relative;
        &::before {
            content: "";
            position: absolute;
            top: 100%;
            left: 0;
            width: 100%;
            height: 1px;
            transform-origin: 0 0;
            background-color: black;
            transition: transform 0.3s ease-out;
        }
        &::after {
            content: "";
            position: absolute;
            top: 100%;
            left: 0;
            width: 100%;
            height: 1px;
            transform-origin: 100% 100%;
            background-color: black;
            transition: transform 0.3s ease-out;
        }
    }

    &__button-is-disabled {
        pointer-events: none;
        &::before {
            transform: rotate(-21deg) scale(1.07);
        }
        &::after {
            transform: rotate(21deg) scale(1.07);
        }
    }


}