
.modal {
     position: fixed;
     z-index: 5;
     top: 0;
     left: 0;
     right: 0;
     bottom: 0;
     background-color: rgba( #FFFFFF, 0.96);
     opacity: 0;
     visibility: hidden;
     transform: scale(1.2);
     transition: all .3s ease-out;
     &--open {
        opacity: 1;
        visibility: visible;
        transform: scale(1);
     }
     &__inner {
         opacity: 0;
         position: absolute;
         transition: opacity 1.8s cubic-bezier(1.000, -0.600, 1.000, 0.690);
        //  top: 35%;
        //  transform: translateY(-50%);
         width: 100%;
         &-is-shown {
            opacity: 1;
         }
     }

     &__close {
         position: absolute;
         top: 6px;
         right: 22px;
         font-size: 2rem;
         font-weight: 600;
         transform: scaleX(1.2);
         transform-origin: 100% 0;
         cursor: pointer;
         &:hover {
            font-weight: 500;
         }
     }

     &__suspense__message {
         padding-top: 100px;
         font-weight: 300;
         font-size: 2.875rem;
         text-transform: capitalize;
         margin: 0 auto;
         text-align: center;
         opacity: 0;
         animation: 1s fadein ease-out forwards;
     }
}

