@import "mixins";

.navbar {
    align-self: center;
    @include for-tablet-portrait-up {
        align-self: initial;
    }
    ul {
        margin: 0;
        padding: 0;
    }

    li {
        list-style: none;
        float: left;
        padding-right: 20px;
        padding-top: 8px;
        
        &:last-child {
            padding-right: 0;
        }
    }

    a {
        color: black;
        font-weight: 300;
        text-decoration: none;
        cursor: pointer;
        transition: 0.3s;
        &.Navbar__anchor-is-current-link {
            font-weight:  600;
        }
        &:hover {
            color: #8F747E;
        }
    }

    &__container {
        opacity: 0;
        transform: scale(1.2);
        transition: all .3s ease-out;
        position: relative;
        z-index: -10;
        padding-top: 66px;
        display: flex;
        justify-content: center;
        // align-items: center;
        width:100%;
        flex-direction: column;

        & > * {
            margin: 10px;
            @include for-tablet-portrait-up {
                margin: 0 12px 0 0;
            }
        }

        @include for-tablet-portrait-up {
            flex-direction: row;
            opacity: 1;
            z-index: 1;
            padding-top: 0;
            transform: scale(1);
            width: 70%;
            justify-content: flex-end;
            align-items: initial;
        }

        @include for-tablet-landscape-up {
            margin-right: 18px;
            flex-direction: row;
            width: 70%;
        }
        &--is-visible {
            opacity: 1;
            z-index: 1;
            transform: scale(1);
        }
    }

    &__language__select {
        font-family: 'Gloria Hallelujah', cursive;
        font-style: italic;
        appearance: none;
        border-radius: 0px;
        background: transparent;
        cursor: pointer;
        outline: none;
        border: 2px solid black;
        padding: 6px;
        transition: all .4s ease-out;
        text-align: center;
        text-align-last: center;
    }
}