@import "mixins";
.headline {
    font-weight: 300;
    font-size: 2.875rem;
    text-transform: capitalize;
    margin: 0 auto;
    text-align: center;
    &__styling-breakbar {
        width: 250px;
        height: 3px;
        background-color: #555;
        border: 0;
        margin-bottom: 40px;
    }

    &__title {
        margin: 0 10px;
        padding: 0 0px;
        font-weight: 600;
        text-align: center;
        &--has-buttons {
            @include for-tablet-portrait-up {
                margin-top: -70px;
                margin-bottom: 45px;
            }
        }
    }
    
    &__description {
        max-width: 775px;
        margin: 30px auto;
        line-height: 2;
        padding: 0 20px;
        text-align: center;
        &-is-hide {
            display: none;
        }
        &-is-shown {
            display: inherit;
            opacity: 0;
            animation: 1s fadein forwards;
        }
    }

    &__anchor {
        color: inherit;
        font-weight: 600;
        cursor:pointer;  
        text-decoration: none;
    }

    &__anchor:hover {
        color: #8F747E;
        transition: 400ms;
    }
    
    &__buttons {
        margin: 0 auto;
        max-width: 600px;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    
    &__button-left {
        margin-left: 25px;
        margin-bottom: 40px;
        position: relative;
        &::before {
            content: "";
            position: absolute;
            top: 100%;
            left: 0;
            width: 100%;
            height: 1px;
            transform-origin: 0 0;
            background-color: black;
            transition: transform 0.3s ease-out;
        }
    }
    &__button-right {
        margin-right: 25px;
        margin-bottom: 40px;
        position: relative;
        &::after {
            content: "";
            position: absolute;
            top: 100%;
            left: 0;
            width: 100%;
            height: 1px;
            transform-origin: 100% 100%;
            background-color: black;
            transition: transform 0.3s ease-out;
        }
    }

    &__button-disabled {
        pointer-events: none;
        &::before {
            transform: rotate(-21deg) scale(1.07);
        }
        &::after {
            transform: rotate(21deg) scale(1.07);
        }
    }
}
